<template>
  <Dialog
    :model-value="modelValue"
    :show-close="false"
    :can-close="false"
    :title="t('title')"
    @update:model-value="emitModelValue"
  >
    <template #desktop>
      <Timeline :progress="progress" :items="timelineItems"></Timeline>
    </template>
    <template #bottom>
      <el-button type="primary" :disabled="!newId" @click="goToThePage">
        {{ t("to") }}
      </el-button>
      <el-button
        type="primary"
        :disabled="!newId"
        @click="emitModelValue(false)"
      >
        {{ t("close") }}
      </el-button>
    </template>
    <template #mobile>
      <Timeline :progress="progress" :items="timelineItems"></Timeline>
      <el-button type="primary" :disabled="!newId" @click="goToThePage">
        {{ t("to") }}
      </el-button>
      <el-button
        type="primary"
        :disabled="!newId"
        @click="emitModelValue(false)"
      >
        {{ t("close") }}
      </el-button>
    </template>
  </Dialog>
</template>

<script lang="ts" setup>
// Scope: Transient

import { UserOperatorFactory } from "@EaseShare/business";
import { INftInformation } from "@EaseShare/business/generated/src/nftInfomation";
import { computed, ref } from "vue";
import { useI18n } from "vue-i18n";
import { useRouter } from "vue-router";
import { useStore } from "vuex";
import Dialog from "../Dialog.vue";
import Timeline from "../Timeline.vue";
import type { ITimelineItem } from "../types";

const { t } = useI18n({
  messages: {
    en: {
      title: "Minting",
      steps: {
        mint: {
          before: "Mint",
          doing: "Minting",
          after: "Minted",
        },
      },
      to: "Go to your NFT",
    },
    "zh-CN": {
      title: "铸造中",
      steps: {
        mint: {
          before: "铸造",
          doing: "铸造中",
          after: "铸造完成",
        },
        to: "前往NFT页面",
      },
    },
  },
});

const props = defineProps<{
  modelValue: boolean;
  metadata: INftInformation;
  nftId: string;
}>();

const emit = defineEmits<{ (e: "update:modelValue", value: boolean): void }>();

function emitModelValue(value: boolean) {
  emit("update:modelValue", value);
}

const store = useStore();
const factory = computed(
  () => store.getters["web3/userOperatorFactory"] as UserOperatorFactory
);

const newId = ref("");

async function mint() {
  const nftId = props.nftId;
  const shop = factory.value.shop;
  const { nftId: _newId } = await shop.buy(nftId, props.metadata.sellingPrice);
  newId.value = _newId;
}

const router = useRouter();

function goToThePage() {
  router.push({ name: "art", params: { nftId: newId.value } });
}

const timelineItems = [] as ITimelineItem[];
timelineItems.push({
  before: t("steps.mint.before"),
  doing: t("steps.mint.doing"),
  after: t("steps.mint.after"),
});
const progress = computed(() => (newId.value ? 1 : 0));

mint();
</script>
